@import url('https://fonts.googleapis.com/css?family=Oswald|Glegoo|Josefin+Sans|Pacifico|Staatliches&display=swap');



.App {
  text-align: center;

}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}
.textcenter{
  text-align: center;
  padding: 20px;
  margin:0px;
  font-family: sans-serif;
}
.heading, .navbar-brand, .nav-item{
  text-align: center;
  font-family: 'Staatliches';
  background-color: #890A34;
  padding:10px;
  margin: 5px;
  height:70px;
  color:white;
text-align: left;
font-size: 27px;

}
.nav-link{
  color: white;
  font-size: 20px;

}

h3{
  text-decoration-style: solid;
  text-transform: capitalize;
  font-size: 40px;
  text-align: left;
}

.hh{
  text-align: center;
  font-family: 'Pacifico';
  color: white;
  font-size: 30px;
}
